.header {
  position: fixed;
  width: 100%;
  top: 0;
  background: #7174cf;
  -webkit-box-shadow: 0 .3rem .5rem #00000080;
          box-shadow: 0 .3rem .5rem #00000080;
  z-index: 5;
}

.header nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: auto;
  height: 5rem;
}

.header ul {
  margin: 1rem 0;
  padding: 0;
  font-size: 1.8rem;
  font-weight: bold;
}

.header li {
  display: inline;
  padding: 0 1rem;
}

a {
  padding: .65rem 0;
  color: white;
  text-decoration: none;
  -webkit-transition: color .3s;
  -o-transition: color .3s;
  transition: color .3s;
  letter-spacing: 1px;
}

a:visited {
  color: white;
}

a:hover {
  color: #75dfff;
}

.selected {
  border-bottom: .4rem solid #75dfff;
}

@media (min-width: 40rem) {
  .header nav {
    width: 70%;
    -ms-flex-pack: end;
        justify-content: flex-end;
  }
}


